.shopsBlock {
  flex-direction: column;
}

.itemShop {
  /* width: 100px;
  height: 100px; */
  text-align: center;
  border: 1px solid rgba(177, 188, 202, 0.72);
  padding: 10px;
  margin: 10px;
}

.logoShop{
  width: 100%;
  height: 50px;
}

.shopInfo{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.buttonShop {
  display: flex;
  align-items: center;
  padding: 10px;
  background: linear-gradient(to left, #4C84FF, #A154F2);
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 13px;
  color: #F2F2F2;
  border-radius: 15px;
}
